.home {
    padding: 5rem 4rem;
}
.home-container {
    display: flex;
    justify-content: center;
    align-item: center;
}
.sambutan{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.sambutan-img{
    flex: 2;
}
.sambutan-img img{
    width: 400px;
}
.sambutan-desc{
    flex: 2;
    width: 400px;
}
.separator{
    width: 30px;
    height: 2px;
    background: #666666;
}
.sambutan-desc h1{
    padding-bottom: 1rem;
    font-size: 35px;
    color: #dd3333;
}
.sambutan-desc p{
    padding: 1rem 0;
    color: #666666;
}
.nama{
    font-weight: bold;
    font-style: italic;
}