.navbar{
    padding: 0 4rem;
    background: #D35D6E;
}
.navbar-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.navbar-logo{
    flex: 1;
}
.navbar-logo img{
    width: 100px;
    height: auto;
}
.navbar-links{
    display: flex;
    flex: 5;
    justify-content: center;
    gap: 20px;
}
a{
    text-decoration: none;
    color: white;
}
.navbar-social{
    flex: 1;
    display: flex;
    flex-direction: row-reverse;
    gap: 10px;
}
.navbar-social img{
    width: 30px;
    border: 1px white solid;
    border-radius: 50%;
}
.navbar-social img:hover{
    fill: #000000;
}
.social{
}
.dropdown{

}
.menu{
    list-style: none;
    display: none;
}