.gallery{
    padding: 2rem 4rem;
}
.top{
    text-align: center;
}
.image-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr) ;
    grid-row-gap: 2rem;
    grid-column-gap: .77rem .0rem;

}
.images {
    width: 550px;
    border-radius: 5px;
    transition: .3s;
    cursor: pointer;
}
.images:hover{
    z-index: 2;
    display: block;
    width: 600px;
}
#popup-root .popup-content{
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 10px;
    background: black;
    color: white;
}
.popup-content button{
    background: red;
    width: 100px;
    height: 40px;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    transition: .3s;
}
.popup-content button:hover{
    background: rgb(129, 3, 3);
}
.upload{
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button-popup {
    width: 100px;
    height: 40px;
    background: red;
    border: none;
    color: white;
    font-size: 15px;
    border-radius: 10px;
    transition: .3s;
    cursor: pointer;
}
.button-popup:hover{
    background: rgb(129, 3, 3);
}

#popup-root .popup-content .success-msg{
    color: white;
}
.message{
    padding: 1rem 0;
    width: 350px;
    border-radius: 5px;
    text-align: center;
}
.input{
    padding: 2rem 0;
}