.footer{
    background: black;
    color: white;
    padding: 2rem 4rem;
    height: 400px;
}
.container-top{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    padding: 2rem 0;
}
.f-left a,
.f-mid1 a,
.f-mid2 a,
.f-mid3 a{
    color: #666666;
}
.f-left a:hover,
.f-mid1 a:hover,
.f-mid2 a:hover,
.f-mid3 a:hover{
    color: red;
    transition: .3s;
}
span{
    color: #666666;
}
h1{
    font-size: 18px;
}
.f-left{
    flex: 1;
}
.left-img img{
    width: 150px;
}
.left-contact p{
    display: flex;
    align-items: center;
    gap: 10px;
}

.left-social{
    padding: 2rem 0;
    display: flex;
    gap: 10px;
}
.left-social img{
    width: 30px;
    border: 1px solid white;
    border-radius: 50%;
}
.f-mid1,
.f-mid2,
.f-mid3{
    padding: 1rem 0 0 0;
    flex: 1;
    display: flex;
    flex-direction: column;
    line-height: 30px;
}
.separator2{
    height: 2px;
    background: #666666;
}
.container-bot{
    padding: 1rem 0;
    color: #666666;
}
.container-bot a{
    color: #666666;
}
.container-bot a:hover{
    text-decoration: underline;
}